import * as C from '../types';

export const AboutInfo = (state = { isFetching: true }, action) => {
	switch (action.type) {
		case C.FETCH_ABOUT_INFO:
			return { ...state, isFetching: true };
		case C.FINISHED_FETCH_ABOUT_INFO:
			return { ...state, isFetching: false, ...action.data };
		case C.ERROR_FETCH_ABOUT_INFO:
			return {
				...state,

				isFetching: false,
				error: action.error,
			};
		default:
			return state;
	}
};

export default AboutInfo;
